export default {
	data() {
		return {
			globalUser: {}
		}
	},
	onShow() {
		let me = this;
		let globalUser = me.getGlobalUser("globalUser");
		me.globalUser = globalUser;
	},
	methods: {
		//性别相关
		modifySex() {
			uni.navigateTo({
				url: "../sex/sex"
			})
		},
		//生日相关
		modifyBirthday() {
			uni.navigateTo({
				url: "../meBirthday/meBirthday"
			})
		},
		//昵称相关
		modifyNickName() {
			uni.navigateTo({
				url: "../meNickName/meNickName",
			});
		},
		// 头像相关
		operator() {
			let me = this;
			let globalUser = me.getGlobalUser("globalUser"); //全局用户对象

			uni.showActionSheet({
				itemList: ["预览头像", "从相册选择头像"],
				success(res) {
					let index = res.tapIndex;
					if (index == 0) {
						//预览
						let faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls: faceArr,
							current: faceArr[0]
						})
					} else if (index == 1) {
						//上传头像
						uni.chooseImage({
							count: 1,
							sizeType: ["compressed"],
							sourceType: ["album"],
							success(res) {
								// 获得临时路径
								var tempFilePath = res.tempFilePaths[0];






								uni.navigateTo({	//图片剪裁插件
									url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath
								})

							}
						});
					};
				}
			})
		},
		cleanStorage() { //清理缓存
			uni.clearStorage();
			uni.showToast({
				title: "清理成功！",
				mask: false,
				duration: 1500
			});

		},
		//退出
		logout() {
			let me = this;
			let globalUser = me.getGlobalUser("globalUser");

			let serverUrl = me.serverUrl; //方式二
			//请求数据
			uni.request({
				url: serverUrl + '/user/logout?userId=' + globalUser.id + '&qq=' + 309063773,
				method: "POST",
				success: (res) => {
					//获取数据之前先判断是否请求成功（200）
					if (res.data.status == 200) {
						//退出成功
						uni.removeStorageSync("globalUser");
						uni.switchTab({
							url: "../me/me"
						});
					}
				}
			});
		}
	}
}
